@import '../variables';

.header {
  border-bottom: 2px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  &__logo {
    display: flex;
    

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  } 

  &__menu {
    display: flex;
    gap: 1em;
    justify-content: space-around;
  }

  .search__placeholder {
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    padding: 10px 10px;
    min-width: 400px;
    margin: 0px auto;
    border-radius: 30px;
    
    input {
      padding-left: 10em;
      color: #8f8f8f;
      font-size: 1rem;
      width: 100%;
      height: 30px;
      line-height: 30px;
      border: transparent;
    } 
  }

  .search__result {
    color: #8f8f8f;
  }

}





